    <div class="container">
      <p>
        <router-link to="/about">About</router-link>
      </p>
      <p>O Calendrio Dissidente  um projeto que discute a memria grfica dos principais acontecimentos sociopolticos do Brasil desde a posse do presidente Bolsonaro em 1 de janeiro de 2019. A discusso  feita pelo mapeamento das estticas das imagens no banco de dados na rede social Instagram.</p>
      <p>Neste site, apresentam-se os resultados parciais da pesquisa de doutorado da designer grfico Didiana Prata, no curso de Ps-Graduao em Design da FAUUSP, sob orientao da Profa. Dra. Giselle Beiguelman e desenvolvida na residncia de Prata no Centro de Inteligncia Artificial do Inova USP, sob coordenao do Prof. Dr. Fabio Cozman.</p>
      <p>O Calendrio Dissidente problematiza questes relacionadas ao arquivamento das imagens nas redes sociais e o seu vocabulrio esttico. Assume que a participao online e diria de artistas, designers, coletivos e do pblico em geral na produo de linguagem visual e de objetos visuais constitui uma frao significativa da memria grfica<sup><a href="#nota-1">1</a></sup> brasileira contempornea.</p>
      <p>Fadadas ao esquecimento, devido ao fluxo de superproduo e veiculao nas redes s quais pertencem, essas imagens so aqui catalogadas e arquivadas, a partir da anlise de algumas hashtags.</p>
      <p>Para tanto, utilizaram-se recursos de inteligncia artificial e aprendizagem de mquina no processo de classificao e arquivamento das imagens coletadas. Considera-se que as hashtags estabelecidas pela autora, como filtro de busca, so representativas das dissidncias e das manifestaes coletivas e poticas de todos ns frente ao momento atual da nossa histria. Essas imagens apresentam novos paradigmas para o estudo do design de comunicao da era ps-internet.</p>
      <h2>Primeira etapa: calendrio streaming</h2>
      <p>O site captura, indexa e publica diariamente as trs imagens mais curtidas em #designativista, #desenhospelademocracia, #mariellepresente, #coleraalegria.</p>
      <p>A partir de uma varredura algortmica, rastreia os principais temas relacionados s imagens e oferece ao usurio os filtros temticos correspondentes, sendo possvel assim verificar quais assuntos foram mais ilustrados.</p>
      <h2>Imagens em destaque</h2>
      <p>Todas as imagens publicadas esto creditadas a partir do nome do usurio que a publicou no Instagram (sendo possvel acessar a pgina do autor). Como vrias imagens so viralizadas e repetidas, adotou-se o critrio de creditar a primeira publicao referente quela imagem.</p>
      <h2>Segunda etapa: uso de inteligncia artificial</h2>
      <p>A segunda fase do projeto apresentar uma curadoria de imagens selecionadas a partir de seis categorias estticas preestabelecidas pela autora: factuais; ilustrao digital; ilustrao manual; tipografia digital; tipografia vernacular e apropriao. Utilizando inteligncia artificial e aprendizagem de mquina na criao de classificadores de imagens, o site publicar narrativas visuais organizadas a partir de categorias estticas, possibilitando assim a apreciao das diversas linguagens visuais das imagens dissidentes das redes. Esta etapa do projeto est prevista para ir ao ar em junho de 2020.</p>
      <h2>Equipe interdisciplinar</h2>
      <p>Participam da programao do site e do desenvolvimento dos classificadores de imagens a equipe do Centro de Inteligncia Artificial do Inova USP, composta pelos alunos e cientistas de dados Gustavo Polleti e Vinicius Akira Imaizumi (Poli-USP), Gustavo Braga (Insper) e pelo programador independente Marcelo Villela Gusmo. Utilizaram-se softwares livres para a captura e programao das funcionalidades do calendrio, assim como no processo de treinamento de mquina e na criao dos rotuladores de imagens. O projeto tambm integra as pesquisas do Grupo de Pesquisa CNPq/FAUUSP Estticas da Memria no Sculo 21 e  apoiado pelo <a href="http://outrosurbanismos.fau.usp.br/" target="_blank" rel="noopener">Laboratrio para OUTROS Urbanismos</a>. </p>
      <p id="nota-1"><small><sup>1</sup> A expresso memria grfica circunscreve uma linha de estudos, conforme assinalam Priscila Lena Farias e Marcos Braga, que busca compreender a importncia e o valor de artefatos visuais, em particular impressos efmeros, na criao de um sentido de identidade local.</small></p>
